import React, { useEffect } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import VacanciesExportPage from './VacanciesExportPage/VacanciesExportPage'
import CompaniesExportPage from './CompaniesExportPage/CompaniesExportPage'

const HRExportPage = ({ setSnack }) => {
  const location = useLocation()
  const navigate = useNavigate()
  useEffect(() => {
    if (!location?.state?.vacancies && !location?.state?.companies) navigate(-1)
  }, [])
  return location?.state?.vacancies ? (
    <VacanciesExportPage setSnack={setSnack} />
  ) : (
    <CompaniesExportPage setSnack={setSnack} />
  )
}

export default React.memo(HRExportPage)
