import React, { useEffect, useState } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import { Box } from '@mui/material'
import Button from '@material-ui/core/Button'
import FormHelperText from '@mui/material/FormHelperText'
import { format } from 'date-fns'
import Filters from '../../../../components/Sub/Filters/Filters'
import useFilters from '../../../../hooks/useFilters'
import useStyles from './CompaniesExportPage.style'
import MultipleSelect from '../../../../components/Sub/ExportDialogs/MultipleSelect/MultipleSelect'
import { API_COMPANIES_EXPORT, API_VACANCIES_EXPORT } from '../../../../utils/constants'
import useAsync from '../../../../hooks/useAsync'
import getCSV from '../../../../utils/getCSV'
import CheckboxItem from '../../../../components/Sub/CheckboxGroup/CheckboxItem/CheckboxItem'
import { useAppSelector } from '../../../../services/store/hooks'

const CompaniesExportPage = ({ setSnack }) => {
  const location = useLocation()
  const navigate = useNavigate()
  const classes = useStyles()
  const hook = useFilters()
  // чекбоксы
  const [vacancy, setVacancy] = useState(false)
  const [company, setCompany] = useState(false)
  const [comment, setComment] = useState(false)
  const [response, setResponse] = useState(false)
  const [offered, setOffered] = useState(false)
  // Селекты
  const [courses, setCourses] = useState([])
  const [scopes, setScopes] = useState([])
  const [sizes, setSizes] = useState([])
  const [status, setStatus] = useState([])
  const [types, setTypes] = useState([])
  // Валидация
  const [isCourseError, setIsCourseError] = useState(false)
  const [isStatusError, setIsStatusError] = useState(false)
  const [isScopesError, setIsScopesError] = useState(false)
  const [isSizesError, setIsSizesError] = useState(false)
  const [isTypesError, setIsTypesError] = useState(false)
  const [serverError, setServerError] = useState(null)
  const [isFiltersError, setIsFiltersError] = useState(null)
  const { directoriesData, filtersJSON } = useAppSelector((store) => store.companiesPage)

  const { run, error } = useAsync({
    data: [],
    error: null,
  })

  useEffect(() => {
    if (!location?.state?.companies) navigate(-1)
  }, [])

  useEffect(() => setServerError(error), [error])

  const checkCheckboxes = () => vacancy || company || comment || response || offered

  const validate = () => {
    const isAnyCheckboxChecked = checkCheckboxes()
    setIsFiltersError(!isAnyCheckboxChecked)
    setIsCourseError(!courses.length)
    setIsStatusError(!status.length)
    setIsSizesError(!sizes.length)
    setIsScopesError(!scopes.length)
    setIsTypesError(!types.length)
    return (
      isAnyCheckboxChecked && !!courses.length && !!status.length && !!sizes.length && !!scopes.length && !!types.length
    )
  }

  const getParams = () => {
    const result = new URLSearchParams()
    // Даты (выпилили, потому что на бэке не было дат создания компаний)
    // result.append('start_date', format(new Date(hook.filters?.start_date), 'yyyy-MM-dd'))
    // result.append('end_date', format(new Date(hook.filters?.end_date), 'yyyy-MM-dd'))
    // Строка поиска
    if (hook.filters?.search) result.append('search', hook.filters?.search)
    // Курс
    if (courses.length > 0) courses.forEach((item) => result.append('courses', item))
    // Статус
    if (status.length > 0) status.forEach((item) => result.append('status', item))
    // Сфера
    if (scopes.length > 0) scopes.forEach((item) => result.append('scope', item))
    // Размер
    if (sizes.length > 0) sizes.forEach((item) => result.append('size', item))
    // Тип
    if (types.length > 0) types.forEach((item) => result.append('type', item))
    // Чекбоксы
    if (vacancy) result.append('fields', 'кол-во вакансий')
    if (company) result.append('fields', 'компания')
    if (comment) result.append('fields', 'комментарий')
    if (response) result.append('fields', 'кол-во откликов')
    if (offered) result.append('fields', 'кол-во трудоустроенных')
    return result
  }

  useEffect(() => {
    if (courses.length > 0) setIsCourseError(false)
  }, [courses.length])

  useEffect(() => {
    if (scopes.length > 0) setIsScopesError(false)
  }, [scopes.length])

  useEffect(() => {
    if (status) setIsStatusError(false)
  }, [status.length])

  useEffect(() => {
    if (sizes.length > 0) setIsSizesError(false)
  }, [sizes.length])

  useEffect(() => {
    if (types.length > 0) setIsTypesError(false)
  }, [types.length])

  useEffect(() => {
    if (isFiltersError) setIsFiltersError(!checkCheckboxes())
  }, [vacancy, company || comment || response || offered])

  const handleSubmit = () => {
    const isValid = validate()
    if (isValid) run(getCSV(API_COMPANIES_EXPORT, getParams()))
  }

  return (
    <>
      s
      <main className={classes.root}>
        <Filters filterType="HRExport" hook={hook} filtersJSON={directoriesData} isCompany />
        <Box className={classes.optionsBox}>
          <Box className={classes.optionCheckboxColumn}>
            <Box className={classes.checkboxGroupHeadItem}>
              <CheckboxItem
                sx={{ fontSize: '18px', lineHeight: '24px', fontWeight: '500' }}
                checkboxLabel="Компания"
                checked={company}
                onChange={() => setCompany(!company)}
                margin={{ marginBottom: 0 }}
              />
            </Box>
            <Box className={classes.checkboxGroupHeadItem}>
              <CheckboxItem
                checkboxLabel="Кол-во вакансий"
                checked={vacancy}
                onChange={() => setVacancy(!vacancy)}
                margin={{ marginBottom: 0 }}
              />
            </Box>

            <Box className={classes.checkboxGroupHeadItem}>
              <CheckboxItem
                checkboxLabel="Кол-во откликов"
                checked={response}
                onChange={() => setResponse(!response)}
                margin={{ marginBottom: 0 }}
              />
            </Box>

            <Box className={classes.checkboxGroupHeadItem}>
              <CheckboxItem
                checkboxLabel="Кол-во трудоустроенных"
                checked={offered}
                onChange={() => setOffered(!offered)}
                margin={{ marginBottom: 0 }}
              />
            </Box>

            <Box className={classes.checkboxGroupHeadItem}>
              <CheckboxItem
                checkboxLabel="Комментарий"
                checked={comment}
                onChange={() => setComment(!comment)}
                margin={{ marginBottom: 0 }}
              />
            </Box>
          </Box>
          <Box className={classes.optionSelectColumn}>
            <MultipleSelect
              multiple
              title="Курсы"
              placeholder="Выбери необходимый курс"
              options={
                directoriesData?.courses ? directoriesData?.courses?.map((c) => ({ id: c.id, name: c.name })) : []
              }
              valueState={[courses, setCourses]}
              error={isCourseError}
              helperText="Выбери курс, чтобы выгрузить данные"
              sx={{ width: 386 }}
              isLong
            />
            <MultipleSelect
              multiple
              title="Статус компании"
              placeholder="Выбери из списка"
              options={filtersJSON?.statuses ? filtersJSON?.statuses?.map((c) => ({ id: c.id, name: c.value })) : []}
              valueState={[status, setStatus]}
              error={isStatusError}
              helperText="Выбери статус компании, чтобы выгрузить данные"
              sx={{ width: 386 }}
              isLong
            />
          </Box>
          <Box className={classes.optionSelectColumn}>
            <MultipleSelect
              multiple
              title="Сфера"
              placeholder="Выбери из списка"
              options={directoriesData?.scopes ? directoriesData?.scopes?.map((c) => ({ id: c.id, name: c.name })) : []}
              valueState={[scopes, setScopes]}
              error={isScopesError}
              helperText="Выбери сферу, чтобы выгрузить данные"
              sx={{ width: 386 }}
              isLong
            />

            <MultipleSelect
              multiple
              title="Размер"
              placeholder="Выбери из списка"
              options={directoriesData?.sizes ? directoriesData?.sizes?.map((c) => ({ id: c.id, name: c.name })) : []}
              valueState={[sizes, setSizes]}
              error={isSizesError}
              helperText="Выбери размер, чтобы выгрузить данные"
              sx={{ width: 386 }}
              isLong
            />

            <MultipleSelect
              multiple
              title="Тип"
              placeholder="Выбери из списка"
              options={directoriesData?.types ? directoriesData?.types?.map((c) => ({ id: c.id, name: c.name })) : []}
              valueState={[types, setTypes]}
              error={isTypesError}
              helperText="Выбери тип, чтобы выгрузить данные"
              sx={{ width: 386 }}
              isLong
            />
          </Box>
        </Box>
        <Box sx={{ marginTop: '17px', alignItems: 'center', flexDirection: 'column' }} className={classes.optionsRow}>
          {isFiltersError && (
            <FormHelperText sx={{ color: 'red' }}>Выбери хотя бы один фильтр для выгрузки</FormHelperText>
          )}
          {serverError && (
            <FormHelperText sx={{ color: 'red' }}>{serverError?.message || 'Произошла ошибка'}</FormHelperText>
          )}
          {!serverError && !isFiltersError && <FormHelperText> </FormHelperText>}
          <Button className={classes.submitButton} onClick={handleSubmit}>
            Выгрузить
          </Button>
        </Box>
      </main>
    </>
  )
}

export default React.memo(CompaniesExportPage)
