import React, { useEffect, useState } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import { Box } from '@mui/material'
import Button from '@material-ui/core/Button'
import FormHelperText from '@mui/material/FormHelperText'
import { format } from 'date-fns'
import Filters from '../../../../components/Sub/Filters/Filters'
import useFilters from '../../../../hooks/useFilters'
import useStyles from './VacanciesExportPage.style'
import MultipleSelect from '../../../../components/Sub/ExportDialogs/MultipleSelect/MultipleSelect'
import { API_VACANCIES_EXPORT } from '../../../../utils/constants'
import useAsync from '../../../../hooks/useAsync'
import getCSV from '../../../../utils/getCSV'
import CheckboxItem from '../../../../components/Sub/CheckboxGroup/CheckboxItem/CheckboxItem'
import { useAppSelector } from '../../../../services/store/hooks'

const VacanciesExportPage = ({ setSnack }) => {
  const location = useLocation()
  const navigate = useNavigate()
  const classes = useStyles()
  const hook = useFilters()
  // чекбоксы
  const [vacancy, setVacancy] = useState(false)
  const [company, setCompany] = useState(false)
  const [comment, setComment] = useState(false)
  const [view, setView] = useState(false)
  const [response, setResponse] = useState(false)
  const [offered, setOffered] = useState(false)
  // Селекты
  const [courses, setCourses] = useState([])
  const [source, setSource] = useState([])
  const [manager, setManager] = useState([])
  const [status, setStatus] = useState([])
  // Валидация
  const [isCourseError, setIsCourseError] = useState(false)
  const [isStatusError, setIsStatusError] = useState(false)
  const [isSourceError, setIsSourceError] = useState(false)
  const [isManagerError, setIsManagerError] = useState(false)
  const [serverError, setServerError] = useState(null)
  const [isFiltersError, setIsFiltersError] = useState(null)
  const { filtersJSON } = useAppSelector((store) => store.vacanciesPage)

  const { run, error } = useAsync({
    data: [],
    error: null,
  })

  useEffect(() => {
    if (!location?.state?.vacancies) navigate(-1)
  }, [])

  useEffect(() => setServerError(error), [error])

  const checkCheckboxes = () => vacancy || company || comment || view || response || offered

  const validate = () => {
    const isAnyCheckboxChecked = checkCheckboxes()
    setIsFiltersError(!isAnyCheckboxChecked)
    setIsCourseError(!courses.length)
    setIsStatusError(!status.length)
    setIsManagerError(!manager.length)
    setIsSourceError(!source.length)
    return isAnyCheckboxChecked && !!courses.length && !!status.length && !!manager.length && !!source.length
  }

  const getParams = () => {
    const result = new URLSearchParams()
    // Даты
    result.append('start_date', format(new Date(hook.filters?.start_date), 'yyyy-MM-dd'))
    result.append('end_date', format(new Date(hook.filters?.end_date), 'yyyy-MM-dd'))
    // Строка поиска
    if (hook.filters?.search) result.append('search', hook.filters?.search)
    // Курс
    if (courses.length > 0) courses.forEach((item) => result.append('courses', item))
    // Статус
    if (status.length > 0) status.forEach((item) => result.append('vacancy_status', item))
    // ответственный менеджер
    if (manager.length > 0) manager.forEach((item) => result.append('hr_profile', item))
    // Источник
    if (source.length > 0) source.forEach((item) => result.append('source', item))
    // Чекбоксы
    if (vacancy) result.append('fields', 'должность')
    if (company) result.append('fields', 'имя компании')
    if (comment) result.append('fields', 'комментарий')
    if (view) result.append('fields', 'кол-во просмотров')
    if (response) result.append('fields', 'кол-во откликов')
    if (offered) result.append('fields', 'кол-во трудоустроенных')
    return result
  }

  useEffect(() => {
    if (courses.length > 0) setIsCourseError(false)
  }, [courses.length])

  useEffect(() => {
    if (source.length > 0) setIsSourceError(false)
  }, [source.length])

  useEffect(() => {
    if (status.length > 0) setIsStatusError(false)
  }, [status.length])

  useEffect(() => {
    if (manager.length > 0) setIsManagerError(false)
  }, [manager.length])

  useEffect(() => {
    if (isFiltersError) setIsFiltersError(!checkCheckboxes())
  }, [vacancy, company || comment || view || response || offered])

  const handleSubmit = () => {
    const isValid = validate()
    if (isValid) run(getCSV(API_VACANCIES_EXPORT, getParams()))
  }

  return (
    <>
      <main className={classes.root}>
        <Filters
          filterType="HRExport"
          hook={hook}
          filtersJSON={filtersJSON}
          handleDownload={undefined}
          handleFeedExport={undefined}
          handleFunnelExport={undefined}
          handleAddCohort={undefined}
          handleAddContact={undefined}
          contactsCount={undefined}
          isStatistics={undefined}
          handleAddVacancy={undefined}
        />
        <Box className={classes.optionsBox}>
          <Box className={classes.optionCheckboxColumn}>
            <Box className={classes.checkboxGroupHeadItem}>
              <CheckboxItem
                sx={{ fontSize: '18px', lineHeight: '24px', fontWeight: '500' }}
                checkboxLabel="Вакансия"
                checked={vacancy}
                onChange={() => setVacancy(!vacancy)}
                margin={{ marginBottom: 0 }}
              />
            </Box>
            <Box className={classes.checkboxGroupHeadItem}>
              <CheckboxItem
                checkboxLabel="Компания"
                checked={company}
                onChange={() => setCompany(!company)}
                margin={{ marginBottom: 0 }}
              />
            </Box>

            <Box className={classes.checkboxGroupHeadItem}>
              <CheckboxItem
                checkboxLabel="Комментарий"
                checked={comment}
                onChange={() => setComment(!comment)}
                margin={{ marginBottom: 0 }}
              />
            </Box>

            <Box className={classes.checkboxGroupHeadItem}>
              <CheckboxItem
                checkboxLabel="Кол-во просмотров"
                checked={view}
                onChange={() => setView(!view)}
                margin={{ marginBottom: 0 }}
              />
            </Box>

            <Box className={classes.checkboxGroupHeadItem}>
              <CheckboxItem
                checkboxLabel="Кол-во откликов"
                checked={response}
                onChange={() => setResponse(!response)}
                margin={{ marginBottom: 0 }}
              />
            </Box>

            <Box className={classes.checkboxGroupHeadItem}>
              <CheckboxItem
                checkboxLabel="Кол-во трудоустроенных"
                checked={offered}
                onChange={() => setOffered(!offered)}
                margin={{ marginBottom: 0 }}
              />
            </Box>
          </Box>
          <Box className={classes.optionSelectColumn}>
            <MultipleSelect
              multiple
              title="Статус вакансии"
              placeholder="Выбери из списка"
              options={filtersJSON?.statuses ? filtersJSON?.statuses?.map((c) => ({ id: c.id, name: c.value })) : []}
              valueState={[status, setStatus]}
              error={isStatusError}
              helperText="Выбери статус вакансии, чтобы выгрузить данные"
              sx={{ width: 386 }}
              isLong
            />
            <MultipleSelect
              multiple
              title="Курс"
              placeholder="Выбери необходимый курс"
              options={filtersJSON?.courses ? filtersJSON?.courses?.map((c) => ({ id: c.id, name: c.name })) : []}
              valueState={[courses, setCourses]}
              error={isCourseError}
              helperText="Выбери курс, чтобы выгрузить данные"
              sx={{ width: 386 }}
              isLong
            />
          </Box>
          <Box className={classes.optionSelectColumn}>
            <MultipleSelect
              multiple
              title="Источник"
              placeholder="Выбери из списка"
              options={filtersJSON?.sources ? filtersJSON?.sources?.map((c) => ({ id: c.id, name: c.name })) : []}
              valueState={[source, setSource]}
              error={isSourceError}
              helperText="Выбери источник, чтобы выгрузить данные"
              sx={{ width: 386 }}
              isLong
            />
            <MultipleSelect
              multiple
              title="Ответственный менеджер"
              placeholder="Выбери ответственного менеджера"
              options={filtersJSON?.profiles ? filtersJSON?.profiles?.map((c) => ({ id: c.id, name: c.name })) : []}
              valueState={[manager, setManager]}
              error={isManagerError}
              helperText="Выбери ответственного менеджера, чтобы выгрузить данные"
              sx={{ width: 386 }}
              isLong
            />
          </Box>
        </Box>
        <Box sx={{ marginTop: '17px', alignItems: 'center', flexDirection: 'column' }} className={classes.optionsRow}>
          {isFiltersError && (
            <FormHelperText sx={{ color: 'red' }}>Выбери хотя бы один фильтр для выгрузки</FormHelperText>
          )}
          {serverError && (
            <FormHelperText sx={{ color: 'red' }}>{serverError?.message || 'Произошла ошибка'}</FormHelperText>
          )}
          {!serverError && !isFiltersError && <FormHelperText> </FormHelperText>}
          <Button className={classes.submitButton} onClick={handleSubmit}>
            Выгрузить
          </Button>
        </Box>
      </main>
    </>
  )
}

export default React.memo(VacanciesExportPage)
